.aboutContainer{
    padding-bottom: 10vmin;
}

.aboutTitle {
    display: flex;
    justify-content: center;
    align-items: center;
}

.aboutTitle > h3 {
    margin-left: 5vmin;
    margin-right: 5vmin;
}

.contentContainer{
    display: flex;
    flex-direction: row;
}

.contentContainer > div {
    width: 33%;
}

.photoCol {
    border-radius: 20px;
    height: 70vmin;
}

.descCol {
    padding-left: 5rem;
    padding-right: 5rem;
}

.photo {
    border-radius: 20px;
    height: 60%;
}

h1 span span {
    position: relative;
    opacity: 0;
    animation: move-text 0.75s forwards;
}

@keyframes move-text {
    0% { opacity: 1; }
    100% { opacity: 1; }
}